<script>
import Video from "video.js";
import "video.js/dist/video-js.css";
import Carousel from "../components/Carousel.vue";
import { Popup } from "vant";
import { ref, reactive, toRefs, getCurrentInstance, watch } from "vue";
import Api from "@/utils/api";
export default {
    name: "Menu",
    components: {
        Popup,
        Carousel,
        Video,
    },
    setup() {
        const { proxy } = getCurrentInstance();
        const show = ref(false);
        const infostart = ref(false);
        const newsTitle = reactive([
            ["最新", "171"],
            ["动态", "172"],
            ["公告", "173"],
            ["活动", "174"],
            ["补给", "175"],
        ]);

        let actived = ref(0);
        const showPopup = () => {
            show.value = true;
            proxy.$refs.vueRef.play();
        };
        const videoPause = () => {
            proxy.$refs.vueRef.pause();
        };

        const infoDate = reactive({
            carphoto: [],
            newsInfo: "",
            roleInfo: [],
        });

        const info = reactive([]);
        const changeInfoStar = ref(true);

        const getNewInfo = async (id) => {
            infoDate.newsInfo = await Api.getInfo.getInfos({
                pageSize: 10,
                pageNum: 1,
                channelId: id,
            });
            // infoDate.newsInfo = results;
            // console.log(infoDate.newsInfo);
            if (changeInfoStar.value) {
                changeInfo();
            }
        };
        getNewInfo(171);

        const changeInfo = async (id, index) => {
            changeInfoStar.value = false;
            if (index != undefined) {
                actived.value = index;
            }
            if (id == undefined) {
                id = 171;
            }
            await getNewInfo(id);
            info.value = [];
            let arr = infoDate.newsInfo.list;
            // console.log(arr);
            arr.forEach((item, index) => {
                if (info.value.length < 4) {
                    if (newsTitle[actived.value][1] == 171) {
                        info.value.push(item);
                    } else {
                        if (item.channelId[0] == newsTitle[actived.value][1]) {
                            info.value.push(item);
                        }
                    }
                }
            });
        };

        const getCG = async () => {
            let results = await Api.getInfo.getInfos({
                pageSize: 6,
                pageNum: 1,
                channelId: 170,
            });
            playerOptions.videoSrc = results.list[0].ext[1].value[0].url;
        };
        getCG();
        const playerOptions = reactive({
            videoSrc: "",
            width: `${document.documentElement.clientWidth}`, // 设置视频播放器的显示宽度（以像素为单位）
            height: `${(document.documentElement.clientWidth / 375) * 210}`,
            preload: "auto", //  建议浏览器是否应在<video>加载元素后立即开始下载视频数据。
            controls: true, // 确定播放器是否具有用户可以与之交互的控件。没有控件，启动视频播放的唯一方法是使用autoplay属性或通过Player API。
            autoplay: "",
        });

        const playerOptionsChange = () => {
            playerOptions.width = document.documentElement.clientWidth;
            playerOptions.height =
                (document.documentElement.clientWidth / 375) * 210;
        };
        window.onresize = playerOptionsChange;
        const getCarPhoto = async () => {
            let results = await Api.getInfo.getInfos({
                pageSize: 6,
                pageNum: 1,
                channelId: 168,
            });
            results.list.forEach((item) => {
                infoDate.carphoto.push(item.ext[0].value[0].url);
            });
        };
        getCarPhoto();

        const getRoleInfo = async () => {
            let results = await Api.getInfo.getInfos({
                pageSize: 200,
                pageNum: 1,
                channelId: 181,
            });
            results.list.forEach((item, index) => {
                if (index < 3) {
                    infoDate.roleInfo.push(item);
                }
            });
            infostart.value = true;
            // console.log(results);
        };
        getRoleInfo();

        let roleActived = ref(0);
        const rolechange = (index) => {
            roleActived.value = index;
        };
        let CGshow = ref(false);
        const CGpopup = () => {
            CGshow.value = true;
            proxy.$refs.CGRef.play();
        };
        const CGPause = () => {
            proxy.$refs.CGRef.pause();
        };
        const scrollAction = () => {
            if (
                proxy.$refs.roleAction.parentElement.getBoundingClientRect()
                    .top +
                    proxy.$refs.roleAction.clientHeight >
                    document.documentElement.clientHeight &&
                proxy.$refs.footer.getBoundingClientRect().y >
                    document.documentElement.clientHeight
            ) {
                proxy.$refs.roleAction.style.position = "absolute";
                proxy.$refs.roleAction.style.top = "0px";
                proxy.$refs.roleAction.style.bottom = "auto";
            } else if (
                proxy.$refs.roleAction.parentElement.getBoundingClientRect()
                    .top +
                    proxy.$refs.roleAction.clientHeight <=
                    document.documentElement.clientHeight &&
                proxy.$refs.footer.getBoundingClientRect().y >
                    document.documentElement.clientHeight
            ) {
                proxy.$refs.roleAction.style.position = "fixed";
                proxy.$refs.roleAction.style.top = "auto";
                proxy.$refs.roleAction.style.bottom = "0px";
            } else if (
                proxy.$refs.footer.getBoundingClientRect().y <=
                document.documentElement.clientHeight
            ) {
                proxy.$refs.roleAction.style.position = "absolute";
                proxy.$refs.roleAction.style.top = "auto";
                proxy.$refs.roleAction.style.bottom = "0px";
            }
        };
        console.log(info);

        return {
            infostart,
            newsTitle,
            actived,
            show,
            showPopup,
            videoPause,
            ...toRefs(playerOptions),
            info,
            changeInfo,
            rolechange,
            roleActived,
            CGshow,
            CGpopup,
            CGPause,
            scrollAction,
            ...toRefs(infoDate),
        };
    },
};
</script>

